import React, {Component} from 'react';
import '../css/layout.css';

class ChatComp extends Component{
	constructor(props){
		super(props);
		
        this.state={
			senderId:"bluc",
			message:null
		}	
	}
	
	componentDidMount(){
		this.sendMessage("hello");
	}
	
	handleKeyPress=(event)=>{
		console.log("www: " + event.target);
		console.log("www: " + event.target.name);
		console.log("www: " + event.target.value);
		
		if(event.charCode === 13){
			this.setState={
				[event.target.name]:event.target.value
			}
			console.log("id: " + this.state.senderId);
			console.log("msg: "+ this.state.message);
		}
	}
	
	sendMessage=(msg)=>{
		console.log(msg);
	}
	
	render(){
		return(
            <div className="dimmed_layer_wrapper">
                <div className="full_layer">
                    <div className="common_alert"> 
	    				<div class="header">
	        				CHAT
					    </div>
					    <div class="chat">
					        <ul>
					        </ul>
					    </div>
					    <div class="input-div">
					        <textarea 
								placeholder="Press Enter for send message."
								name="message"
								onKeyPress={this.handleKeyPress}/>
					    </div>
	
					    <div class="chat format">
					        <ul>
					            <li>
					                <div class="sender">
					                    <span></span>
					                </div>
					                <div class="message">
					                    <span></span>
					                </div>
					            </li>
					        </ul>
                    	</div>
                	</div>
            	</div>
			</div>
		);
	}
}
export default ChatComp;
